import React, { useState, useEffect } from 'react';
import axios from 'axios';
import '../style/header.css';
import { Link, useLocation } from 'react-router-dom';
const API_BASE_URL = process.env.REACT_APP_API_BASE_URL;
const levelToPath = {
  3: '/admin',
  2: '/teacher',
  1: '/student',  // 1   
};


function Header() {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [userLevel, setUserLevel] = useState(null);
  const [username, setUsername] = useState('');
  const location = useLocation();
  const currentPath = location.pathname;

  // State ,     
const [menuOpen, setMenuOpen] = useState(false);


useEffect(() => {
  console.log("Menu is now: ", menuOpen);
}, [menuOpen]);
//    
const toggleMenu = () => {

  console.log("Before toggle: ", menuOpen);
  setMenuOpen(!menuOpen);
  console.log("After toggle: ", menuOpen);
};

function useWindowWidth() {
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);

  useEffect(() => {
    const handleResize = () => {
      setWindowWidth(window.innerWidth);
    };

    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  return windowWidth;
}


const windowWidth = useWindowWidth();

  useEffect(() => {
    const checkLoginStatus = async () => {
      try {
        const response = await axios.get(`${API_BASE_URL}/checkLogin`);
        if (response.data.isLoggedIn) {
          setIsLoggedIn(true);
          setUsername(response.data.username);
          setUserLevel(response.data.userLevel);
        } else {
          setIsLoggedIn(false);
          setUsername('');
          setUserLevel(null);
          if (currentPath !== '/') {
            window.location.href = "/";
          }
        }
      } catch (error) {
        alert('     ');
      }
    };
  
    checkLoginStatus();
  }, []);
  
  
  const handleLogout = async () => {
    try {
      const response = await axios.get(`${API_BASE_URL}/logout`);
      if (response.data.success) {
        setIsLoggedIn(false);
        setUsername('');
        setUserLevel(null);
        window.location.href = "/"; //      
      }
    } catch (error) {
      alert('   ');
    }
  };
  
  const handleLogin = async () => {
    const email = document.querySelector('input[type="text"]').value;
    const password = document.querySelector('input[type="password"]').value;
    

    try {
      const response = await axios.post(`${API_BASE_URL}/login`, {
        email,
        password
      });
    
      if (response.data.success) {
        setIsLoggedIn(true);
        setUsername(response.data.username);
        setUserLevel(response.data.userLevel);
      } else {
        alert(response.data.message); //    
      }
    } catch (error) {
      if (error.response && error.response.data && error.response.data.message) {
        alert(error.response.data.message); //     
      } else {
        alert('  ');
      }
    }
  };
  const CommonMenu = () => (
    <>
      <div className="common-menu-item">
        <Link to="/">STUDENTS'REVIEW</Link>
      </div>
      <div className="common-menu-item">
        <Link to="/">WHAT IS-KTAL LIVE?</Link>
      </div>
      <div className="common-menu-item">
        <Link to="/">TUTORS</Link>
      </div>
    </>
  );
  const AdminMenu = () => (
    <div className={`admin-menu ${menuOpen ? 'show' : ''}`}>
       <Link to="/admin/class-management"></Link>
        <Link to="/admin/student-management"></Link>
        <Link to="/admin/payment-management"></Link>
        <Link to="/admin/teacher-management"></Link>
        <Link to="/admin/board-management"> </Link>
        <Link to="/admin/book-management"> </Link>
        <Link to="/admin/review-management"> </Link>
        <Link to="/admin/teacher-introduction">  </Link>
    </div>
  );
  
  const TeacherMenu = () => (
    <div className={`admin-menu ${menuOpen ? 'show' : ''}`}>
      <Link to="/teacher/teacher-main"></Link>
      <Link to="/teacher/lecture-fee"></Link>
      <Link to="/teacher/availability"></Link>
      <Link to="/teacher/book-management"></Link>
      <Link to="/teacher/resource-room"></Link>
    </div>
  );
  
  return (
    <header className="header">
      <div className="logo-menu-container">
        <div className="logo">
          <Link to="/"></Link>
        </div>
        {currentPath === '/' ? (
      <>
        <CommonMenu/>
      </>
    ) : null
  }
   </div>
   {(currentPath !== '/') ? (
  <>
    <button className="button toggle-button" onClick={toggleMenu}>=</button>
    <div className={`menu-content ${menuOpen || windowWidth > 768 ? 'menu-open' : ''}`}>
    </div>
  </>
) : null}
 <div className="login button-container">
          {isLoggedIn ? (
            <>
            
             {currentPath === '/' ? null : (
                <>
                   
                  {windowWidth > 768 && (
                    <>
                      {userLevel === 3 && <AdminMenu />}
                      {userLevel === 2 && <TeacherMenu />}
                    </>
                  )}
                </>
              )}
               {(windowWidth <= 768 && menuOpen) && (
            <>
              {userLevel === 3 && <AdminMenu />}
              {userLevel === 2 && <TeacherMenu />}
            </>
          )}
              <span>{username}</span>
              <div className="menu-bar">
                <Link to={userLevel === 3 ? '/admin' : userLevel === 2 ? '/teacher' : '/student'}>
                  <button className="button menu-item"></button>
                </Link>
                <button className="button menu-item" onClick={handleLogout}></button>
              </div>
            </>
          ) : (
           
          <div className="row">
          <div className="col-12 col-md-3">
            <div className="form-group">
              <input type="text" className="form-control" placeholder="" />
            </div>
          </div>
          <div className="col-12 col-md-3">
            <div className="form-group">
              <input type="password" className="form-control" placeholder="" />
            </div>
          </div>
          <div className="col-6 col-md-2">
            <button className="btn btn-primary" onClick={handleLogin}></button>
          </div>
          <div className="col-6 col-md-2">
            <Link to="/signup">
              <button className="btn btn-secondary"></button>
            </Link>
          </div>
        </div>
          )}
        </div>
            </header>
            );
          };
          export default Header