import React, { useState ,useEffect   } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import axios from 'axios';


const API_BASE_URL = process.env.REACT_APP_API_BASE_URL;

const TeacherManagement = () => {
  const [errorMessage, setErrorMessage] = useState(null);
  const [showModal, setShowModal] = useState(false);
  const [email, setEmail] = useState('');
  const [memberName, setMemberName] = useState('');
  const [gender, setGender] = useState('');
  const [phoneNumber, setPhoneNumber] = useState('');
  const [address, setAddress] = useState('');
  const [detailAddress, setDetailAddress] = useState('');
  const [registrationNumberFront, setRegistrationNumberFront] = useState('');
  const [registrationNumberBack, setRegistrationNumberBack] = useState('');
  const [teachingLanguage, setTeachingLanguage] = useState('');
  const [bankName, setBankName] = useState('');
  const [accountNumber, setAccountNumber] = useState('');
  const [zoomMeetingLink, setZoomMeetingLink] = useState('');
  const [teacher, setTeachers] = useState([]);
  
  const toggleModal = () => {
    setShowModal(!showModal);
  };
  
  useEffect(() => {
    const fetchTeachers = async () => {
      try {
        const response = await axios.get(`${API_BASE_URL}/teacherLoad`); // API     
        setTeachers(response.data);
      } catch (error) {
        console.error('   .', error);
      }
    };

    fetchTeachers();
  }, []); //  dependency array       
  const handleSubmit = async () => {
  const combinedRegistrationNumber = `${registrationNumberFront}-${registrationNumberBack}`;
    try {
      const formData = {
        email,
        memberName,
        phoneNumber,
        gender,
        address,
        detailAddress,
        registrationNumber: combinedRegistrationNumber, 
        teachingLanguage,
        bankName,
        accountNumber,
        zoomMeetingLink,
      };
  
      const response = await axios.post(`${API_BASE_URL}/TeacherCreate`, JSON.stringify(formData), {
        headers: {
          'Content-Type': 'application/json',
        },
      });

      if (response.status === 201) {
        console.log('  .');
        console.log(API_BASE_URL);
      }
  
      toggleModal(); //  
    } catch (error) {
      console.error('  .', error);
      console.log(API_BASE_URL);
    }
  };


    return (
      <div className="container">
        <h1 className="mt-3 mb-3"> </h1>
        <div className="mb-3 d-flex justify-content-between">
          <div>
            <button className="btn btn-primary"> </button>
            <button className="btn btn-secondary ml-2"> </button>
            <button className="btn btn-secondary ml-2"> </button>
          </div>
          <button className="btn btn-success" onClick={toggleModal}> </button>
        </div>
        {showModal && (
        <div className="modal fade show d-block" tabIndex="-1">
          <div className="modal-dialog">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title"> </h5>
                <button type="button" className="btn-close" onClick={toggleModal}></button>
              </div>
          <div className="modal-body">
            <div className="row mb-3">
              <label className="col-sm-3 col-form-label"></label>
              <div className="col-sm-9">
                <input type="text" placeholder="" value={email} onChange={(e) => setEmail(e.target.value)} className="form-control" />
              </div>
            </div>
            <div className="row mb-3">
              <label className="col-sm-3 col-form-label"> </label>
              <div className="col-sm-9">
                <input type="text" placeholder=" " value={memberName} onChange={(e) => setMemberName(e.target.value)} className="form-control" />
              </div>
            </div>
            <div className="row mb-3">
              <label className="col-sm-3 col-form-label"> :</label>
              <div className="col-sm-9">
              <select id="gender" value={gender} onChange={(e) => setGender(e.target.value)} className="form-control">
                <option value=""></option>
                <option value=""></option>
              </select>
              </div>
            </div>
            <div className="row mb-3">
              <label className="col-sm-3 col-form-label"></label>
              <div className="col-sm-9">
                <input type="text" placeholder="" value={phoneNumber} onChange={(e) => setPhoneNumber(e.target.value)} className="form-control" />
              </div>
            </div>
            <div className="row mb-3">
                  <label className="col-sm-3 col-form-label"> </label>
                  <div className="col-sm-9">
                    <input
                      style={{ width: '100%' }} 
                      value={address} 
                      onChange={e => setAddress(e.target.value)}
                      placeholder=""
                    />
                  </div>
                </div>
                <div className="row mb-3">
                  <label className="col-sm-3 col-form-label"> </label>
                  <div className="col-sm-9">
                    <input
                      style={{ width: '100%' }} 
                      value={detailAddress}
                      onChange={e => setDetailAddress(e.target.value)}
                      placeholder=""
                    />
                  </div>
                </div>

          <div className="row mb-3">
            <label className="col-sm-3 col-form-label"></label>
            <div className="col-sm-4">
              <input type="text" placeholder="" value={registrationNumberFront} onChange={(e) => setRegistrationNumberFront(e.target.value)} className="form-control" />
            </div>
          <div className="col-sm-1 text-center align-self-center">
            <span style={{ fontSize: '24px' }}>-</span>
          </div>
            <div className="col-sm-4">
              <input type="text" placeholder="" value={registrationNumberBack} onChange={(e) => setRegistrationNumberBack(e.target.value)} className="form-control" />
            </div>
          </div>
          <div className="row mb-3">
            <label className="col-sm-3 col-form-label">  </label>
            <div className="col-sm-9">
              <input type="text" placeholder="  " value={teachingLanguage} onChange={(e) => setTeachingLanguage(e.target.value)} className="form-control" />
            </div>
          </div>
          <div className="row mb-3">
            <label className="col-sm-3 col-form-label"> </label>
            <div className="col-sm-9">
              <input type="text" placeholder=" " value={bankName} onChange={(e) => setBankName(e.target.value)} className="form-control" />
            </div>
          </div>
          <div className="row mb-3">
            <label className="col-sm-3 col-form-label"> </label>
            <div className="col-sm-9">
              <input type="text" placeholder=" " value={accountNumber} onChange={(e) => setAccountNumber(e.target.value)} className="form-control" />
            </div>
          </div>
          <div className="row mb-3">
            <label className="col-sm-3 col-form-label">  </label>
            <div className="col-sm-4">
              <input type="text" placeholder="" value={zoomMeetingLink} onChange={(e) => setZoomMeetingLink(e.target.value)} className="form-control" />
            </div>
        </div>
          
        </div>
        <div className="modal-footer">
          <button type="button" className="btn btn-secondary" onClick={toggleModal}></button>
          <button type="button" className="btn btn-primary" onClick={handleSubmit}></button>
        </div>
      </div>
    </div>
  </div>
)}
  <table className="table">
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th> </th>
              <th> </th>
              <th>   </th>
              <th> </th>
            </tr>
          </thead>
          <tbody>
          {teacher.map((singleTeacher, index) => (
              <tr key={index}>
                <td>{singleTeacher.id}</td>
                <td>{singleTeacher.name}</td>
                <td>{singleTeacher.email}</td>
              </tr>
            ))}
          </tbody>
        </table>
        <button className="btn btn-info"> </button>
      </div>
    );
  };

export default TeacherManagement;
